/* @import url(//fonts.googleapis.com/css?family=Open+Sans:400,700); */
#_pi_surveyWidgetContainer {
  z-index: 1031;
}

#_pi_surveyWidgetCustom {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
  position: fixed;
  top: 0;
  left: 0;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  z-index: 1032;
  -moz-transition: 0.2s;
  -ms-transition: 0.2s;
  -o-transition: 0.2s;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  font-size: 16px;
  z-index: 18000;
  overflow: hidden;
}

#_pi_surveyWidget {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
  position: fixed;
  max-height: 0px;
  height: auto;
  width: 300px;
  top: 0px;
  left: 0px;
  margin: 10%;
  width: calc(100% - 20%) !important;
  position: fixed;
  display: flex !important;
  flex-direction: column !important;
  flex-wrap: nowrap !important;
  justify-content: space-between !important;
  align-items: center !important;
  align-content: center !important;
  background-color: #181818;
  text-align: center;
  z-index: 1032;
  -moz-transition: 0.2s;
  -ms-transition: 0.2s;
  -o-transition: 0.2s;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  font-size: 16px;
  z-index: 18000;
  overflow: hidden;
}

._pi_closeCustomButton {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
  position: absolute;
  top: 6px;
  right: 10px;
  color: #000;
  cursor: pointer;
}

._pi_closeButton {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
  position: absolute;
  top: 6px;
  right: 10px;
  color: #fff;
  cursor: pointer;
}

._pi_invitationTextContainer {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
  color: white;
}

._pi_background_image {
  max-width: 100%;
}

._pi_startButton {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
  padding: 8px 12px;
  background-color: black;
  color: white;
}

._pi_startButton:hover {
  text-decoration: none;
}

._pi_loadingSurvey,
._pi_thankYouSurvey {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
  color: white;
}

._pi_pollContainer {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
  color: white;
  padding: 30px 0px 30px 0px;
  text-align: left;
}

._pi_pollChoice {
  margin-top: 2px;
  margin-bottom: 2px;
  margin-left: 7px;
  overflow: hidden;
  position: relative;
  display: block;
  cursor: inherit;
  text-overflow: ellipsis;
  white-space: nowrap;
}

._pi_pollChart {
  background-color: rgba(255, 255, 255, 0.15);
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  transition: all 0.3s cubic-bezier(0.5, 1.2, 0.5, 1.2);
  opacity: 1;
}

._pi_pollProgress {
  display: inline-block;
  max-width: 100%;
  margin-bottom: 30px;
  margin-left: 7px;
  font-size: 12px;
  overflow: hidden;
}

._pi_pollProgressPercentage {
  display: inline-block;
}

._pi_pollProgressCount {
  display: inline-block;
  margin-left: 5px;
}

._pi_pollText {
  position: relative;
  display: inline-block;
}

._pi_pollText span {
  vertical-align: middle;
}

._pi_pollName {
  font-size: 14px;
  margin-left: 7px;
}

._pi_question {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
  color: white;
  text-align: left;
  padding: 0px 20px;
  margin-top: 20px;
}

._pi_question_custom_content_question_fullscreen {
  color: black;
}

._pi_answers_container {
  margin-top: 10px;
}

ul._pi_answers_container,
div._pi_answers_container {
  list-style-type: none;
  padding: 0px 0px 20px 0px;
  margin: 10px 0px;
  width: 100%;
  overflow: hidden;
}

ul._pi_answers_container li {
  background-color: rgba(255, 255, 255, 0.05);
  text-align: left;
  margin: 15px 0px;
  padding: 0px 20px;
  width: 100%;
  overflow: hidden;
  max-width: 460px;
  float: left;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

ul._pi_answers_container li:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

ul._pi_answers_container li a {
  position: relative;
  display: block;
  color: white;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
  margin: 0px;
  padding: 0px 20px 0px 0px;
  text-decoration: none;
}

ul._pi_answers_container li a img {
  margin: 5px 0;
}

ul._pi_answers_container li a label.with-image {
  float: none !important;
  margin-left: 5px;
  width: auto;
}

ul._pi_answers_container li a label {
  color: white;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
  width: 100%;
  width: -moz-calc(100% - 35px);
  width: -webkit-calc(100% - 35px);
  width: calc(100% - 35px);
  float: left;
  cursor: pointer;
  margin: 0px 0px 0px 25px;
  padding: 5px 0px 5px 0px;
}

ul._pi_answers_container li a span._pi_radio_button_outer {
  position: absolute;
  top: 10px;
  left: 0px;
  display: inline-block;
  float: left;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  width: 14px;
  height: 14px;
}

ul._pi_answers_container li a span._pi_radio_button_inner,
ul._pi_answers_container li a span._pi_checkbox_inner {
  position: absolute;
  top: 2px;
  left: 2px;
  width: 8px;
  height: 8px;
}

ul._pi_answers_container li:hover a span._pi_radio_button_inner,
ul._pi_answers_container li.selected a span._pi_radio_button_inner {
  background-color: rgba(255, 255, 255, 0.5);
  border: 1px solid transparent;
  border-radius: 50%;
}

a._pi_branding {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
  color: white;
  font-size: 10px;
  position: absolute;
  bottom: 8px;
  left: 8px;
}

a._pi_branding:hover {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
  color: white;
  font-size: 10px;
  text-decoration: none;
}

div._pi_all_questions_submit_button_container {
  margin-bottom: 25px;
  clear: both;
}

input._pi_all_questions_submit_button {
  background-color: black;
  font-size: 15px;
  padding: 5px 20px;
  border: none;
  color: #eaf4fb;
  cursor: pointer;
}

input._pi_all_questions_submit_button:hover {
  background-color: #101010;
}

input._pi_all_questions_submit_button:disabled {
  opacity: 0.3;
}

input._pi_all_questions_submit_button:hover:disabled {
  background-color: black;
  opacity: 0.3;
}

._pi_all_questions_error {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
  text-align: center;
  font-size: 12px;
  color: #927a85;
  margin-top: 10px;
}

div._pi_free_text_question_field_container {
  margin: 0px 18px 10px 18px;
}

input._pi_free_text_question_field,
textarea._pi_free_text_question_field {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
