  height: 17px;
}

._pi_invitationTextContainer {
  margin: 10px 20px;
  display: inline-block;
}

._pi_startButton {
  display: inline-block;
  margin-top: 5px;
}

._pi_loadingSurvey,
._pi_thankYouSurvey {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
  color: white;
  margin: 50px 0px 80px 0px;
  text-align: center;
}

._pi_surveyWidget {
  text-align: center;
  height: auto;
}

._pi_widgetContentContainer {
  margin-left: auto;
  margin-right: auto;
  width: 960px;
  text-align: center;
}

._pi_closeButton {
  padding: 8px;
}

@media screen and (max-width: 960px) {
  ._pi_widgetContentContainer {
    width: 100%;
  }
}

#_pi_surveyWidgetContainer.mobile-enabled {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.5);
}

#_pi_surveyWidgetContainer.mobile-enabled #_pi_surveyWidget {
  width: 90%;
  position: relative;
  top: auto;
  left: auto;
  right: auto;
  bottom: auto;
  margin-top: 10%;
  margin-left: auto;
  margin-right: auto;
  border: 3px solid rgba(174, 174, 174, 0.6);
  border-radius: 5px;
}

#_pi_surveyWidgetContainer.mobile-enabled ._pi_closeButton {
  background-color: rgba(255, 255, 255, 0.1);
  width: 40px;
  height: 40px;
  font-size: 20px;
  line-height: 40px;
}

#_pi_surveyWidgetContainer.mobile-enabled ._pi_widgetContentContainer {
  margin-top: 10px;
}

